import React, { useState, useEffect, useRef } from "react";
import { pages, chouonData } from "./data";
import Sample from "./sample";
import Dropdown from "./dropdown";
import "./chouon.css";

const Chouon = () => {
  const def = pages[3];
  const [expand, setExpand] = useState(-1);
  const timeoutRef = useRef();

  useEffect(() => {
    if (timeoutRef.current) {
      clearTimeout(timeoutRef.current);
    }
    if (expand === -1) {
      timeoutRef.current = null;
    } else {
      timeoutRef.current = setTimeout(() => {
        timeoutRef.current = null;
        setExpand(-1);
      }, 12000);
    }
  }, [expand]);

  return (
    <div style={{ width: 740, height: 620, backgroundColor: def.base }}>
      <div
        style={{
          position: "absolute",
          top: 30,
          left: 20,
          width: 120,
          color: "#bbb",
          fontSize: "0.92em",
          fontWeight: 400,
          lineHeight: 2,
        }}
      >
        <p>本页罗列了需要发长音的8种情况。</p>
        <p>标记长音的符号不发本音，而是拖长它之前的那个假名的发音时间，一般延长一倍。</p>
      </div>

      <div style={{ position: "absolute", left: 200, top: 30, display: "inline-block" }}>
        {chouonData.map((value, inx) => {
          return (
            <div style={{ paddingTop: 0, paddingBottom: 10 }}>
              {Array.isArray(value[1]) ? (
                <>
                  <Dropdown
                    width={50}
                    items={value[1]}
                    index={inx}
                    expand={expand}
                    setExpand={setExpand}
                  />
                  <div className="inline" style={{ marginLeft: 10 }}>
                    段假名加<span className="inlineSpan">{value[0]}</span>
                  </div>
                </>
              ) : (
                <div className="inline">
                  {value[1] + "加"}
                  <span className="inlineSpan">{value[0]}</span>
                </div>
              )}
              {value[2].map((v, i) => {
                return <Sample word={v[0]} meaning={v[1]} index={i + inx * 10} />;
              })}
            </div>
          );
        })}
      </div>
    </div>
  );
};
export default Chouon;
