import React, { useState, useEffect, useRef } from "react";
import { pages, consonants, vowels, makeKanaQuiz, makeSoundQuiz } from "./data";
import Board from "./board";
import Speaker from "./speaker";
import SoundPick from "./soundPick";
import Hira from "./hira";
import TextButton from "./textButton";
import "./gojuon.css";
import Choice from "./choice";

const Exercise = () => {
  const cs = [...consonants];
  cs[0] = "∅";
  const def = pages[6];
  const [clear, setClear] = useState(false);
  const [status, setStatus] = useState(-1);
  const [vowel, setVowel] = useState(vowels[0]);
  const [consonant, setConsonant] = useState("∅");
  const [answer, setAnswer] = useState(false);
  const [dakuten, setDakuten] = useState(false);
  const [kana, setKana] = useState(true);
  const [soundQuizSelected, setSoundQuizSelected] = useState();
  const [kanaQuiz, setKanaQuiz] = useState(makeKanaQuiz(false));
  const [soundQuiz, setSoundQuiz] = useState(makeSoundQuiz(false));

  useEffect(() => {
    setSoundQuiz(makeSoundQuiz(dakuten));
    setKanaQuiz(makeKanaQuiz(dakuten));
  }, [dakuten]);

  const pick = (op) => {
    setAnswer(false);
    setStatus(-1);
    if (soundQuizSelected === op) {
      setSoundQuizSelected(null);
    } else {
      setSoundQuizSelected(op);
    }
  };

  const playSound = (src) => {
    if (src) {
      const audio = new Audio("/assets/audio/" + src + ".mp3");
      audio.play();
    }
  };

  const SoundDetect = () => {
    return (
      <>
        <div style={{ position: "absolute", left: 422, top: 40 }}>
          <Hira
            letter={soundQuiz.base}
            size={200}
            weight={500}
            font={"NotoSerifJP"}
            stroke="#0f0"
            strokeWidth={6}
          />
        </div>
        <div style={{ position: "absolute", left: 280, top: 30 }}>
          <SoundPick
            label={{ text: "A", sound: soundQuiz.options[0] }}
            selected={soundQuizSelected}
            size="40"
            tone="1"
            onSelect={pick}
          />
        </div>
        <div style={{ position: "absolute", left: 280, top: 90 }}>
          <SoundPick
            label={{ text: "B", sound: soundQuiz.options[1] }}
            selected={soundQuizSelected}
            size="40"
            tone="2"
            onSelect={pick}
          />
        </div>
        <div style={{ position: "absolute", left: 280, top: 150 }}>
          <SoundPick
            label={{ text: "C", sound: soundQuiz.options[2] }}
            selected={soundQuizSelected}
            size="40"
            tone="3"
            onSelect={pick}
          />
        </div>
        <div style={{ position: "absolute", left: 280, top: 210 }}>
          <SoundPick
            label={{ text: "D", sound: soundQuiz.options[3] }}
            selected={soundQuizSelected}
            size="40"
            tone="0"
            onSelect={pick}
          />
        </div>

        {answer && (
          <div
            style={{
              position: "absolute",
              left: 460,
              top: 330,
              fontFamily: "monospace",
              fontSize: "3em",
              fontWeigt: 500,
              color: "#eee",
            }}
          >
            {"[" + soundQuiz.roman + "]"}
          </div>
        )}
      </>
    );
  };

  const vowelSelect = (v) => {
    setAnswer(false);
    setStatus(-1);
    setVowel(v);
  };

  const consonantSelect = (v) => {
    setAnswer(false);
    setStatus(-1);
    setConsonant(v);
  };

  const KanaDetect = () => {
    return (
      <>
        <Board
          size={300}
          base={"#770"}
          label={"#077"}
          opacity={0.5}
          background={"white"}
          clear={clear}
        />
        <div
          style={{
            position: "absolute",
            left: 404,
            top: 30,
            fontSize: 18,
            color: "#eee",
            lineHeight: 2,
          }}
        >
          播放语音文件找出对应假名
        </div>
        <button
          onClick={() => {
            playSound(kanaQuiz.base);
          }}
          className="iconButton"
          style={{ position: "absolute", width: 136, height: 136, left: 440, top: 80 }}
        >
          <Speaker size={128} />
        </button>
        {answer && (
          <div style={{ position: "absolute", left: 440, top: 310 }}>
            <Hira
              letter={kanaQuiz.kana}
              size={124}
              weight={500}
              font={"NotoSerifJP"}
              stroke="#0f0"
              strokeWidth={2}
              roman={"[" + kanaQuiz.answer + "]"}
            />
          </div>
        )}
        {dakuten && (
          <div style={{ position: "absolute", left: 350, top: 240 }}>
            <Choice
              selected={consonant}
              options={["g", "z", "d", "b", "p"]}
              onChange={consonantSelect}
              width="60"
              textColor="#eee"
            />
          </div>
        )}
        <div style={{ position: "absolute", left: 420, top: 240 }}>
          <Choice
            selected={consonant}
            options={cs}
            onChange={consonantSelect}
            width="60"
            textColor="#eee"
          />
        </div>
        <div style={{ position: "absolute", left: 530, top: 240 }}>
          <Choice
            selected={vowel}
            options={vowels}
            onChange={vowelSelect}
            width="60"
            textColor="#eee"
          />
        </div>
      </>
    );
  };

  const showAnswer = () => {
    if (kana) {
      if (!vowel) {
        setStatus(2);
      } else {
        const b = !consonant || consonant === "∅" ? vowel : consonant + vowel;
        setStatus(kanaQuiz.answer === b ? 1 : 0);
      }
    } else {
      if (!soundQuizSelected) {
        setStatus(2);
      } else {
        const i = "ABCD".indexOf(soundQuizSelected);
        setStatus(soundQuiz.answer === soundQuiz.options[i] ? 1 : 0);
      }
    }
    setAnswer(true);
  };

  return (
    <div style={{ position: "relative", width: 740, height: 620, backgroundColor: def.base }}>
      {kana && <KanaDetect />}
      {!kana && <SoundDetect />}
      {status !== -1 && (
        <div
          style={{
            position: "absolute",
            left: 462,
            top: 480,
            width: 96,
            height: 38,
            fontFamily: "serif",
            fontSize: "1.25em",
            fontWeight: 500,
            color: status === 2 ? "#888" : status === 0 ? "#f11" : "#1e1",
          }}
        >
          {status === 2 ? "没有答题" : status === 0 ? "回答错误" : "回答正确"}
        </div>
      )}

      <TextButton
        width={90}
        height={32}
        text={kana ? "辨别语音" : "找出假名"}
        x={240}
        y={540}
        onAction={() => {
          setAnswer(false);
          setStatus(-1);
          setKana(!kana);
        }}
      />

      <TextButton
        width={90}
        height={32}
        text={dakuten ? "排除浊音" : "包括浊音"}
        x={360}
        y={540}
        onAction={() => {
          setAnswer(false);
          setStatus(-1);
          setDakuten(!dakuten);
        }}
      />

      <TextButton
        width={90}
        height={32}
        text={"显示答案"}
        x={480}
        y={540}
        onAction={() => {
          showAnswer();
        }}
      />

      <TextButton
        width={90}
        height={32}
        text={"继续出题"}
        x={600}
        y={540}
        onAction={() => {
          setAnswer(false);
          setStatus(-1);
          setConsonant("∅");
          setVowel(vowels[0]);
          setSoundQuizSelected("");
          setSoundQuiz(makeSoundQuiz(dakuten));
          setKanaQuiz(makeKanaQuiz(dakuten));
        }}
      />
    </div>
  );
};
export default Exercise;
