import React, { useState, useEffect, useRef } from "react";
import { pages, allPieces, randomFrom } from "./data";
import TextButton from "./textButton";
import "./gojuon.css";

const Puzzle = () => {
  const def = pages[5];
  const [pieces, setPieces] = useState();
  const [mark, setMark] = useState(false);
  const [dakuten, setDakuten] = useState(false);
  const [kana, setKana] = useState(false);
  const [selected, setSelected] = useState(-1);
  const cell = 60;

  useEffect(() => {
    doShuffle();
  }, []);

  useEffect(() => {
    doShuffle();
  }, [dakuten]);

  const doShuffle = () => {
    const vs = shuffle(allPieces());
    setPieces(vs);
  };

  const shuffle = (ps) => {
    const n = dakuten ? 71 : 46;
    const a = randomFrom(n);
    for (let i = 0; i < n; i++) {
      ps[i][4] = ps[a[i]][2];
      ps[i][5] = ps[a[i]][3];
    }
    return ps;
  };

  const xof = (col) => {
    return col * cell + 60;
  };

  const yof = (row) => {
    return row * cell + 5;
  };

  const doMark = (v) => {
    return v[2] === v[4] && v[3] === v[5];
  };

  const Fig = ({ letter, index }) => {
    const stroke = mark && doMark(letter) ? "#f62" : selected === index ? "#444" : "none";
    const lineWidth = mark && doMark(letter) ? 4.5 : selected === index ? 3.5 : 0;
    return (
      <svg viewBox="0 0 60 60" width={cell} height={cell}>
        <linearGradient id="ivory" x1="0%" y1="100%" x2="100%" y2="0%">
          <stop stopColor="#e8f8ff" offset="0%" />
          <stop stopColor="#fff8e8" offset="50%" />
          <stop stopColor="#f8ffef" offset="100%" />
        </linearGradient>
        <rect
          x="1.8"
          y="1.8"
          width={56}
          height={56}
          fill="url('#ivory')"
          stroke={stroke}
          strokeWidth={lineWidth}
        />
        <text
          x={30}
          y={30 + 10}
          fontSize={36}
          fontFamily="NotoSerifJP"
          fontWeight={600}
          fill={letter[2] >= 5 ? "#050" : "#500"}
          textAnchor="middle"
        >
          {letter[kana ? 1 : 0]}
        </text>
      </svg>
    );
  };

  const Consonant = ({ type }) => {
    const label =
      type === 0
        ? ["∅", "k", "s", "t", "n", "h", "m", "y", "r", "w"]
        : type === 1
        ? ["g", "z", "d", "b", "p"]
        : ["俺", "开", "桑", "塔", "纳", "蛤", "蟆", "压", "了", "娃"];
    const x = type === 0 ? 8 : 22;
    const ch = type === 0 ? "start" : "end";
    return (
      <svg
        viewBox={`0,0,24,${type === 1 ? 5 * cell : 10 * cell}`}
        width={24}
        height={type === 1 ? 5 * cell : 10 * cell}
      >
        {label.map((a, i) => {
          return (
            <text
              key={a + i}
              x={x}
              y={yof(i) + cell / 2 + 7}
              fontSize={16}
              fontFamily="monospace"
              fill="#999"
              textAnchor={ch}
            >
              {a}
            </text>
          );
        })}
      </svg>
    );
  };

  return (
    <div style={{ position: "relative", width: 740, height: 620, backgroundColor: def.base }}>
      {pieces &&
        pieces.map((p, inx) => {
          return inx >= 46 && !dakuten ? (
            <></>
          ) : (
            <div
              onClick={() => {
                if (selected === -1) {
                  setSelected(inx);
                } else if (selected !== inx) {
                  const o = pieces;
                  const col = o[selected][4];
                  const row = o[selected][5];
                  o[selected][4] = o[inx][4];
                  o[selected][5] = o[inx][5];
                  o[inx][4] = col;
                  o[inx][5] = row;
                  setSelected(-1);
                  setPieces(o);
                }
              }}
              style={{
                position: "absolute",
                border: "2px solid #9bb",
                cursor: "pointer",
                backgroundColor: "#eff",
                width: cell,
                height: cell,
                left: xof(p[4]),
                top: yof(p[5]),
                zIndex: p[6],
                transition: "400ms ease-in-out",
              }}
            >
              <Fig letter={p} index={inx} />
            </div>
          );
        })}
      <div style={{ position: "absolute", left: 9 * cell + 124, top: 0 }}>
        <Consonant type={0} />
      </div>
      {dakuten && (
        <div style={{ position: "absolute", left: 30, top: 0 }}>
          <Consonant type={1} />
        </div>
      )}
      {!dakuten && (
        <div style={{ position: "absolute", left: 328, top: 0 }}>
          <Consonant type={2} />
        </div>
      )}

      {!dakuten && (
        <div
          style={{
            position: "absolute",
            left: 28,
            top: 24,
            fontSize: 20,
            color: "#eee",
            lineHeight: 2,
          }}
        >
          <div>通过两两交换位置，</div>
          <div>令各假名正确复位</div>
        </div>
      )}

      <TextButton
        width={112.5}
        height={40}
        text={dakuten ? "排除浊音" : "包括浊音"}
        x={190}
        y={340}
        onAction={() => {
          setDakuten(!dakuten);
        }}
      />
      <TextButton
        width={112.5}
        height={40}
        text={kana ? "显示片假" : "显示平假"}
        x={190}
        y={405}
        onAction={() => {
          setKana(!kana);
        }}
      />
      <TextButton
        width={112.5}
        height={40}
        text={"重新洗牌"}
        x={190}
        y={470}
        onAction={() => {
          doShuffle();
        }}
      />
      <TextButton
        width={112.5}
        height={40}
        text={mark ? "取消标注" : "标注好牌"}
        x={190}
        y={535}
        onAction={() => {
          setMark(!mark);
        }}
      />
    </div>
  );
};

export default Puzzle;
