import { gradients } from "./data";

export const ColorDef = ({ out, num }) => {
  return (
    <linearGradient id={out ? "border" + num : "body" + num} x1="100%" y1="0%" x2="0%" y2="100%">
      <stop stopColor={gradients[num][out ? 0 : 2]} offset="0%" />
      <stop stopColor={gradients[num][out ? 1 : 3]} offset="100%" />
    </linearGradient>
  );
};

export const ColorDef1 = ({ num, out }) => {
  return (
    <linearGradient id={(out ? "body" : "border") + num} x1="100%" y1="0%" x2="0%" y2="100%">
      <stop stopColor={gradients[num][out ? 0 : 2]} offset="0%" />
      <stop stopColor={gradients[num][out ? 1 : 3]} offset="100%" />
    </linearGradient>
  );
};

export const SpeakerSvg = ({
  num,
  center,
  rad,
  shift,
  dx,
  dy,
  scale,
  type,
  src,
  index,
  onSpeak,
}) => {
  return (
    <g
      transform={`translate(${dx},${dy}) scale(${scale})`}
      cursor="pointer"
      onClick={() => {
        if (onSpeak) {
          onSpeak(src, type, index);
        }
      }}
    >
      <circle
        cx={center}
        cy={center}
        r={rad}
        fill={`url('#body${num}')`}
        stroke={`url('#border${num}')`}
        strokeWidth="4"
      />
      <g transform={`translate(${shift},${shift})`}>
        <path
          d="M39.389,13.769 L22.235,28.606 L6,28.606 L6,47.699 L21.989,47.699 L39.389,62.75 L39.389,13.769z"
          stroke={gradients[num][4]}
          strokeWidth="6"
          strokeLinejoin="round"
          fill={gradients[num][4]}
        />
        <path
          d="M48,27.6a19.5,19.5 0 0 1 0,21.4M55.1,20.5a30,30 0 0 1 0,35.6M61.6,14a38.8,38.8 0 0 1 0,48.6"
          fill="none"
          stroke={gradients[num][4]}
          strokeWidth="6"
          strokeLinecap="round"
        />
      </g>
    </g>
  );
};
