import React, { useState, useEffect, useRef } from "react";
import { pages, sokuonData } from "./data";
import Sample from "./sample";
import Dropdown from "./dropdown";

import "./chouon.css";

const Sokuon = () => {
  const [expand, setExpand] = useState(-1);
  const def = pages[4];
  const timeoutRef = useRef();

  useEffect(() => {
    if (timeoutRef.current) {
      clearTimeout(timeoutRef.current);
    }
    if (expand === -1) {
      timeoutRef.current = null;
    } else {
      timeoutRef.current = setTimeout(() => {
        timeoutRef.current = null;
        setExpand(-1);
      }, 12000);
    }
  }, [expand]);

  return (
    <div style={{ width: 740, height: 620, backgroundColor: def.base }}>
      <div>
        <div className="title">促音形成规则</div>
        <div className="subtitle">促音符号是っ</div>
        <div className="subtitle">外型像小号的つ，</div>
        <div className="subtitle">不发音，但占一个音拍</div>
      </div>
      <div style={{ marginLeft: 200, marginTop: -98, display: "inline-block" }}>
        {sokuonData.map((value, inx) => {
          return (
            <div style={{ paddingTop: 10, paddingBottom: 10 }}>
              <div className="inline" style={{ marginLeft: 10 }}>
                <span className="inlineSpan">っ</span>
                <span className="inlineSpan">于</span>
              </div>
              <Dropdown
                width={50}
                items={value[0]}
                index={inx}
                expand={expand}
                setExpand={setExpand}
              />
              <div className="inline" style={{ marginLeft: 10 }}>
                <span className="inlineSpan">行之前</span>
              </div>
              {value[1].map((v, i) => {
                return <Sample word={v[0]} meaning={v[1]} index={i + inx * 10} />;
              })}
            </div>
          );
        })}
      </div>
    </div>
  );
};
export default Sokuon;
