import React, { useLayoutEffect, useRef, useState } from "react";

import "./choice.css";

const Choice = ({ selected, options, onChange, align, width, textColor }) => {
  const Icon = ({ isOpen }) => {
    return (
      <svg viewBox="0 0 24 24" width="14" height="12" className={isOpen ? "rotArrow" : "normArrow"}>
        <path d="M2 6 L22 6 L12 23 Z" fill="#22a" stroke="none" />
      </svg>
    );
  };

  const [showMenu, setShowMenu] = useState(false);
  const tmRef = useRef();

  useLayoutEffect(() => {
    if (showMenu) {
      if (tmRef.current) {
        clearTimeout(tmRef.current);
      }
      tmRef.current = setTimeout(() => {
        setShowMenu(false);
        tmRef.current = null;
      }, 10000);
    }
  }, [showMenu]);

  const onItemClick = (option) => {
    setShowMenu(false);
    onChange(option);
  };

  const render = (option) => {
    const key = "key" + option;
    if (selected && option === selected) {
      return (
        <div key={key} className="dropdown-item selected">
          {!option ? "" : option}
        </div>
      );
    } else {
      return (
        <div onClick={() => onItemClick(option)} key={key} className="dropdown-item">
          {!option ? "" : option}
        </div>
      );
    }
  };

  const theStyle =
    align && width
      ? { float: align.toString(), width: width.toString() + "px" }
      : align
      ? { float: align.toString() }
      : width
      ? { width: width.toString() + "px" }
      : {};

  return (
    <div className="choice-container" style={theStyle}>
      <div
        className="dropdown-input"
        onClick={() => {
          setShowMenu(!showMenu);
        }}
      >
        <div
          className={selected ? "dropdown-selected" : "dropdown-placeholder"}
          style={{ color: textColor, fontSize: "1.2em" }}
        >
          {selected && options.includes(selected) ? selected : ""}
        </div>
        <div>
          <Icon isOpen={showMenu} />
        </div>
      </div>
      {showMenu && (
        <div
          key={options[0] + "menuPart"}
          className={`dropdown-menu alignment--${align || "auto"}`}
        >
          {options.map((option) => render(option))}
        </div>
      )}
    </div>
  );
};

export default Choice;
