export const allPieces = () => {
  const a = new Array(71);
  for (let i = 0; i < 46; i++) {
    const b = letters[i];
    const col = vowels.indexOf(b[2]);
    const row = b[3] === null ? 0 : consonants.indexOf(b[3]);
    a[i] = [b[0], b[1], col + 5, row, col + 5, row, 0];
  }
  for (let i = 0; i < 25; i++) {
    const b = dakuten[i];
    const col = vowels.indexOf(b[2]);
    const row = "gzdbp".indexOf(b[3]);
    a[i + 46] = [b[0], b[1], col, row, col, row, 0];
  }
  return a;
};

export const pickLetter = (v, c) => {
  for (let i = 0; i < letters.length; i++) {
    const o = letters[i];
    if (v === o[2] && c === o[3]) {
      return o;
    }
  }
  return null;
};

export const pickVoiced = (v, c) => {
  for (let i = 0; i < dakuten.length; i++) {
    const o = dakuten[i];
    if (v === o[2] && c === o[3]) {
      return o;
    }
  }
  return null;
};

