const shuffle = (a) => {
  const n = a.length;
  for (let i = 0; i < n - 1; i++) {
    const j = Math.trunc((n - i) * Math.random()) + i;
    if (j > i) {
      const k = a[i];
      a[i] = a[j];
      a[j] = k;
    }
  }
};

export const randomFrom = (n) => {
  const a = new Array(n);
  for (let i = 0; i < n; i++) {
    a[i] = i;
  }
  shuffle(a);
  return a;
};

export const randomOf = (m, n) => {
  if (m >= n) {
    return randomFrom(n);
  }
  const a = new Array(n);
  for (let i = 0; i < n; i++) {
    a[i] = i;
  }
  const b = new Array(m);
  for (let i = 0; i < m; i++) {
    const j = Math.trunc((n - i) * Math.random()) + i;
    b[i] = a[j];
    a[j] = a[i];
    a[i] = b[i];
  }
  return b;
};

const build = (v, c) => {
  const a = [...letters, ...dakuten];
  const o = [];
  for (let i = 0; i < a.length; i++) {
    const b = a[i];
    if (b[2] === v || b[3] === c) {
      if (b[2] !== v || b[3] !== c) {
        o.push(b);
      }
    }
  }
  return o;
};

const src4 = (f) => {
  const c = f[3];
  if (c !== null && "gzdbp".indexOf(c) >= 0) {
    return f[0];
  }
  return f[4];
};

export const makeKanaQuiz = (includeDakuten) => {
  const n = Math.trunc(
    Math.random() * (includeDakuten ? letters.length + dakuten.length : letters.length)
  );
  const v = n >= letters.length ? dakuten[n - letters.length] : letters[n];
  return { type: 4, base: src4(v), answer: v[4], kana: v[0], options: [] };
};

export const makeSoundQuiz = (includeDakuten) => {
  const n = Math.trunc(
    Math.random() * (includeDakuten ? letters.length + dakuten.length : letters.length)
  );
  const v = n >= letters.length ? dakuten[n - letters.length] : letters[n];
  const f = build(v[2], v[3]);
  const r = randomOf(3, f.length);
  const answer = src4(v);
  let options = [answer, src4(f[r[0]]), src4(f[r[1]]), src4(f[r[2]])];
  shuffle(options);
  return {
    type: 3,
    base: v[Math.random() < 0.6 ? 0 : 1],
    answer: answer,
    roman: v[4],
    options: options,
  };
};

export const makeQuiz = () => {
  let a = randomOf(30, letters.length + dakuten.length);
  const res = [];
  for (let i = 0; i < 30; i++) {
    const z = a[i];
    const e = z >= letters.length ? dakuten[z - letters.length] : letters[z];
    const f = build(e[2], e[3]);
    const r = randomOf(2, f.length);
    let base;
    let answer;
    let options;
    let type;
    if (i < 12) {
      type = 0;
      base = Math.random() < 0.6 ? e[0] : e[1];
      answer = e[4];
      options = [answer, f[r[0]][4], f[r[1]][4]];
    } else if (i < 26) {
      type = i < 18 ? 1 : 2;
      base = i < 18 ? e[4] : src4(e);
      answer = Math.random() < 0.6 ? e[0] : e[1];
      const v1 = Math.random() < 0.6 ? f[r[0]][0] : f[r[0]][1];
      const v2 = Math.random() < 0.6 ? f[r[1]][0] : f[r[1]][1];
      options = [answer, v1, v2];
    } else {
      type = 3;
      base = Math.random() < 0.6 ? e[0] : e[1];
      answer = src4(e);
      const v1 = src4(f[r[0]]);
      const v2 = src4(f[r[1]]);
      options = [answer, v1, v2];
    }
    shuffle(options);
    res.push({ type: type, base: base, answer: answer, options: options });
  }
  return res;
};
