import Speaker from "./speaker";
import "./gojuon.css";

const SoundPick = ({ label, selected, tone, size, onSelect }) => {
  const playSound = (src) => {
    if (src) {
      const audio = new Audio("/assets/audio/" + src + ".mp3");
      audio.play();
    }
  };
  const Label = () => {
    return (
      <svg viewBox="0 0 112 112" width={size} height={size}>
        <text textAnchor="middle" x="56" y={76} fontSize={64} fill="#eee">
          {label.text}
        </text>
        {label.text === selected && (
          <rect
            x={10}
            y={52 - 44}
            width={88}
            height={88}
            strokeWidth={5}
            stroke="#f00"
            fill="none"
          />
        )}
      </svg>
    );
  };

  return (
    <>
      <button
        className="iconButton"
        onClick={() => {
          onSelect(label.text);
        }}
      >
        <Label />
      </button>
      <button
        className="iconButton"
        onClick={() => {
          playSound(label.sound);
        }}
      >
        <Speaker size={size} tone={tone} />
      </button>
    </>
  );
};
export default SoundPick;
