import "./dropdown.css";

const Dropdown = ({ index, items, width, expand, setExpand }) => {
  const Icon = () => {
    return (
      <svg viewBox="0 0 24 24" width="14" height="12">
        <path d="M2 6 L22 6 L12 23 Z" fill="#994" stroke="none" />
      </svg>
    );
  };

  const render = (option) => {
    const key = "key" + option + index;
    return (
      <div
        key={key}
        className="dropdown-item"
        onClick={() => {
          setExpand(-1);
        }}
      >
        {option}
      </div>
    );
  };
  return (
    <div className="choice-container" style={{ width: width }}>
      <div
        className="dropdown-input"
        onClick={() => {
          setExpand(index === expand ? -1 : index);
        }}
      >
        <div className="dropdown-placeholder" style={{ color: "#efe" }}>
          {items[0]}
        </div>
        <div>
          <Icon />
        </div>
      </div>
      {expand === index && (
        <div key={index + "menuPart"} className="dropdown-menu">
          {items.map((option) => render(option))}
        </div>
      )}
    </div>
  );
};

export default Dropdown;
