import React, { useState, useEffect, useRef } from "react";

import "./main.css";
import { pages } from "./data";
import Gujuon from "./gojuon";
import Dakuten from "./dakuten";
import Yoon from "./yoon";
import Chouon from "./chouon";
import Sokuon from "./sokuon";
import Puzzle from "./puzzle";
import Exercise from "./exercise";
import Exam from "./exam";

const Main = () => {
  const [page, setPage] = useState(0);
  const changePage = (inx) => {
    setPage(inx);
  };

  return (
    <div>
      <div style={{ width: 740, height: 620, position: "relative" }}>
        {page === 0 && <Gujuon />}
        {page === 1 && <Dakuten />}
        {page === 2 && <Yoon />}
        {page === 3 && <Chouon />}
        {page === 4 && <Sokuon />}
        {page === 5 && <Puzzle />}
        {page === 6 && <Exercise />}
        {page === 7 && <Exam />}

        {pages.map((item, inx) => {
          const s = 34;
          const off = 620 - 9 * s;
          const y = off + item.index * (s + 4);
          return (
            <div
              key={"div" + inx}
              onClick={() => {
                if (inx !== page) {
                  changePage(inx);
                }
              }}
              className="tab"
              style={{
                position: "absolute",
                width: 140,
                height: s - 6,
                left: 1,
                top: y,
                backgroundColor: item.base,
                paddingLeft: "20px",
                paddingTop: "6px",
                cursor: page === inx ? "none" : "pointer",
                opacity: page === inx ? 0.4 : 1.0,
              }}
            >
              {item.title}
            </div>
          );
        })}
      </div>
    </div>
  );
};
export default Main;
