import { SpeakerSvg, ColorDef } from "./svgPart";

const Speaker = ({ size, tone }) => {
  return (
    <svg viewBox="0 0 112 112" width={size} height={size}>
      <ColorDef out={true} num={tone ? tone : 0} />
      <ColorDef out={false} num={tone ? tone : 0} />
      <SpeakerSvg num={tone ? tone : 0} center={56} rad={54} shift={16} dx={0} dy={0} scale={1} />
    </svg>
  );
};
export default Speaker;
