import React, { useState, useEffect, useRef } from "react";

const Board = ({ size, letter, base, label, opacity, background, clear }) => {
  const canvasRef = useRef();
  const mouseRef = useRef(false);
  const strokeStyle = "#3a3";
  const strokeWidth = (4 * size) / 200;

  const drawStart = (xin, yin) => {
    const rect = canvasRef.current.getBoundingClientRect();
    const x = xin - rect.left;
    const y = yin - rect.top;
    if (x < 0 || x > size || y < 0 || y > size) {
      mouseRef.current = false;
      return;
    }
    const ctx = canvasRef.current.getContext("2d");
    if (
      ((x < 30 || x > size - 30) && (y < 30 || y > size - 30)) ||
      x < 6 ||
      x > size - 6 ||
      y < 6 ||
      y > size - 6
    ) {
      mouseRef.current = false;
      ctx.clearRect(0, 0, size, size);
    } else {
      mouseRef.current = true;
      ctx.beginPath();
      ctx.moveTo(x, y);
    }
  };

  const draw = (xin, yin) => {
    const rect = canvasRef.current.getBoundingClientRect();
    const x = xin - rect.left;
    const y = yin - rect.top;
    if (x < 0 || x >= size || y < 0 || y >= size) {
    } else {
      const ctx = canvasRef.current.getContext("2d");
      ctx.strokeStyle = strokeStyle;
      ctx.lineWidth = strokeWidth;
      ctx.lineTo(x, y);
      ctx.stroke();
    }
  };

  const mouseDown = (e) => {
    drawStart(e.clientX, e.clientY);
  };

  const mouseUp = (e) => {
    mouseRef.current = false;
  };

  const mouseMove = (e) => {
    if (mouseRef.current) {
      draw(e.clientX, e.clientY);
    }
  };

  const touchStart = (e) => {
    const touch = e.touches[0];
    drawStart(touch.clientX, touch.clientY);
  };

  const touchMove = (e) => {
    if (mouseRef.current) {
      const touch = e.touches[0];
      draw(touch.clientX, touch.clientY);
    }
  };

  const touchEnd = (e) => {
    mouseRef.current = false;
  };

  useEffect(() => {
    canvasRef.current.addEventListener("mousedown", mouseDown);
    canvasRef.current.addEventListener("mousemove", mouseMove);
    canvasRef.current.addEventListener("mouseup", mouseUp);
    canvasRef.current.addEventListener("touchstart", (e) => {
      touchStart(e);
    });
    canvasRef.current.addEventListener("touchend", (e) => {
      touchEnd(e);
    });
    canvasRef.current.addEventListener("touchmove", (e) => {
      touchMove(e);
    });
  }, []);

  useEffect(() => {
    const ctx = canvasRef.current.getContext("2d");
    ctx.clearRect(0, 0, size, size);
  }, [clear]);

  return (
    <div style={{ width: size, height: size }}>
      <div style={{ zIndex: 0, position: "absolute" }}>
        <svg viewBox="0 0 54 54" width={size} height={size}>
          <defs>
            <marker refX="0" refY="0" orient="auto" overflow="visible" id="Arrow">
              <path
                d="M 0,0 L 5,-5 L -12.5,0 L 5,5 L 0,0 z "
                transform="matrix(-0.4,0,0,-0.4,-4,0)"
                fill={label}
                stroke={label}
                strokeWidth="1pt"
                strokeLinejoin="round"
              />
            </marker>
          </defs>
          {background && (
            <rect x="0" y="0" width="54" height="54" fill={background} stroke="none" />
          )}
          <path
            d="M0.25,0.25 L0.25,53.75 L53.75,53.75 L53.75,0.25 Z M27,0.25 L27,53.75 M0.25,27 L53.75,27"
            stroke="#555"
            strokeWidth="0.5"
            fill="none"
            strokeDasharray="2,2"
            opacity="0.4"
          />
          <path d="M1.6,1.6L4.0,4.0 M1.6,4.0L4.0,1.6" fill="none" stroke="#888" strokeWidth={0.6} />

          {letter && <path d={letter.path} fill={base} stroke="none" opacity={opacity} />}
          {letter &&
            letter.labels.map((path, index) => {
              return (
                <path
                  key={index + "path"}
                  d={path}
                  fill="none"
                  stroke={label}
                  strokeWidth="1"
                  markerEnd="url(#Arrow)"
                  opacity={0.4 * opacity}
                />
              );
            })}
          {letter &&
            letter.notes.map((note, index) => {
              return (
                <text
                  key={index + "text"}
                  x={note.x}
                  y={note.y}
                  fill={label}
                  fontSize="8"
                  opacity={0.6 * opacity}
                >
                  {note.v}
                </text>
              );
            })}
        </svg>
      </div>
      <div style={{ zIndex: 1, position: "absolute" }}>
        <canvas ref={canvasRef} width={size} height={size} />
      </div>
    </div>
  );
};
export default Board;
