const Hira = ({ letter, size, font, weight, stroke, strokeWidth, fontSize, textColor, roman }) => {
  const defFntSize = 200;
  const defTextColor = "#eee";

  return (
    <svg
      viewBox={`0 0 300 ${roman ? 360 : 300}`}
      width={size}
      height={roman ? (size * 12) / 10 : size}
    >
      <rect
        x="1"
        y="1"
        width="298"
        height="298"
        stroke={stroke}
        strokeWidth={strokeWidth}
        fill="none"
      />
      <text
        x="150"
        y="230"
        fontFamily={font}
        fontSize={fontSize ? fontSize : defFntSize}
        fontWeight={weight}
        fill={textColor ? textColor : defTextColor}
        textAnchor="middle"
      >
        {letter}
      </text>
      {roman && (
        <text
          x="150"
          y="350"
          fontFamily="monospace"
          fontSize={(fontSize ? fontSize : defFntSize) / 4}
          fontWeight={400}
          fill={textColor ? textColor : defTextColor}
          textAnchor="middle"
        >
          {roman}
        </text>
      )}
    </svg>
  );
};

export default Hira;
