import React, { useState, useEffect, useRef } from "react";
import { pages, letters, vowels, consonants, pickLetter, hiragana, katakana } from "./data";
import Board from "./board";
import Hira from "./hira";
import TextButton from "./textButton";
import "./gojuon.css";
import { SpeakerSvg, ColorDef } from "./svgPart";

const Gojuon = () => {
  const [value, setValue] = useState(pickLetter("a", null));
  const [letter, setLetter] = useState(hiragana.a);
  const [kana, setKana] = useState(false);
  const [clear, setClear] = useState(false);
  const audioRef = useRef();
  const def = pages[0];

  useEffect(() => {
    if (value !== undefined) {
      const arry = kana ? katakana : hiragana;
      setLetter(arry[value[4]]);
    }
  }, [value, kana]);

  const Vowel = ({ width, height }) => {
    return (
      <svg viewBox="0 0 290 20" width={width} height={height}>
        <text x={29} y="16" fontSize={16} fontFamily="monospace" fill="#777" textAnchor="middle">
          a
        </text>
        <text x={87} y="16" fontSize={16} fontFamily="monospace" fill="#777" textAnchor="middle">
          i
        </text>
        <text x={145} y="16" fontSize={16} fontFamily="monospace" fill="#777" textAnchor="middle">
          u
        </text>
        <text x={203} y="16" fontSize={16} fontFamily="monospace" fill="#777" textAnchor="middle">
          e
        </text>
        <text x={261} y="16" fontSize={16} fontFamily="monospace" fill="#777" textAnchor="middle">
          o
        </text>
      </svg>
    );
  };

  const SvgText = ({ width, height, label, text }) => {
    return (
      <svg viewBox="0 0 220 32" width={width} height={height}>
        <text x={120} y="20" fontSize={16} fontFamily="sans-serif" fill="#ddd" textAnchor="end">
          {label}
        </text>
        <text
          x={120}
          y="20"
          fontSize={20}
          fontWeight={600}
          fontFamily="serif"
          fill="#eee"
          textAnchor="start"
        >
          {text}
        </text>
      </svg>
    );
  };

  const Consonant = ({ width, height }) => {
    return (
      <svg viewBox="0 0 24 580" width={width} height={height}>
        <text x={20} y="42" fontSize={16} fontFamily="monospace" fill="#777" textAnchor="end">
          ∅
        </text>
        <text x={20} y="100" fontSize={16} fontFamily="monospace" fill="#777" textAnchor="end">
          k
        </text>
        <text x={20} y="158" fontSize={16} fontFamily="monospace" fill="#777" textAnchor="end">
          s
        </text>
        <text x={20} y="216" fontSize={16} fontFamily="monospace" fill="#777" textAnchor="end">
          t
        </text>
        <text x={20} y="274" fontSize={16} fontFamily="monospace" fill="#777" textAnchor="end">
          n
        </text>
        <text x={20} y="332" fontSize={16} fontFamily="monospace" fill="#777" textAnchor="end">
          h
        </text>
        <text x={20} y="390" fontSize={16} fontFamily="monospace" fill="#777" textAnchor="end">
          m
        </text>
        <text x={20} y="448" fontSize={16} fontFamily="monospace" fill="#777" textAnchor="end">
          y
        </text>
        <text x={20} y="506" fontSize={16} fontFamily="monospace" fill="#777" textAnchor="end">
          r
        </text>
        <text x={20} y="564" fontSize={16} fontFamily="monospace" fill="#777" textAnchor="end">
          w
        </text>
      </svg>
    );
  };

  const Icon = ({ size }) => {
    return (
      <svg viewBox="0 0 120 120" width={size} height={size}>
        <ColorDef out={false} num={0} />
        <ColorDef out={true} num={0} />
        <SpeakerSvg num={0} center={60} rad={58} shift={22} dx={0} dy={0} scale={1} />
      </svg>
    );
  };

  const TextIcon = ({ size }) => {
    return (
      <svg viewBox="0 0 32 32" width={size} height={size}>
        <ColorDef out={false} num={0} />
        <ColorDef out={true} num={0} />
        <circle
          cx="16"
          cy="16"
          r="15.2"
          fill="url('#body0')"
          stroke="url('#border0')"
          strokeWidth="1.4"
        />
        {value && (
          <text
            x={16}
            y={24}
            textAnchor="middle"
            fontWeight={500}
            fontSize={20}
            fontFamily="NotoSansJP"
            fill="#eee"
          >
            {value[kana ? 0 : 1]}
          </text>
        )}
      </svg>
    );
  };

  const play = () => {
    if (audioRef.current) {
      audioRef.current.play();
    }
  };

  const select = (v, c) => {
    setClear(!clear);
    const o = pickLetter(v, c);
    setValue(o);
    audioRef.current = new Audio("/assets/audio/" + o[4] + ".mp3");
    play();
  };

  const cell = (v, c, i, j) => {
    const o = pickLetter(v, c);
    if (o === null) return <></>;
    const sel = !value ? false : value[2] === v && value[3] === c;
    return (
      <div
        key={i + "div" + j}
        onClick={() => {
          select(v, c);
        }}
        style={{
          position: "absolute",
          left: j * 58 + 440,
          top: 28 + i * 58,
          width: 58,
          height: 58,
          cursor: "pointer",
        }}
      >
        <Hira
          letter={kana ? o[1] : o[0]}
          size={58}
          weight={500}
          font={"NotoSerifJP"}
          stroke={sel ? "#f00" : "#0f0"}
          strokeWidth={sel ? 12 : 2}
        />
      </div>
    );
  };

  const row = (c, i) => {
    return vowels.map((v, j) => {
      return cell(v, c, i, j);
    });
  };

  return (
    <div style={{ width: 740, height: 620, backgroundColor: def.base }}>
      <button
        className="iconButton"
        onClick={() => {
          setKana(!kana);
        }}
        style={{
          position: "absolute",
          left: 340,
          top: 40,
          width: 52,
          height: 52,
        }}
      >
        <TextIcon size={48} />
      </button>
      <button
        className="iconButton"
        onClick={() => {
          play();
        }}
        style={{
          position: "absolute",
          left: 340,
          top: 120,
          width: 52,
          height: 52,
        }}
      >
        <Icon size={48} />
      </button>
      <TextButton
        width={90}
        height={32}
        text={"清除画板"}
        x={320}
        y={200}
        onAction={() => {
          setClear(!clear);
        }}
      />
      <div style={{ position: "absolute", left: 8, top: 8, width: 300, height: 300 }}>
        <Board
          size={300}
          letter={letter}
          base={"#770"}
          label={"#077"}
          opacity={0.5}
          background={"white"}
          clear={clear}
        />
      </div>
      <div style={{ position: "absolute", left: 440, top: 8, width: 300, height: 20 }}>
        <Vowel width={290} height={20} />
      </div>
      <div style={{ position: "absolute", left: 414, top: 18, width: 24, height: 580 }}>
        <Consonant width={24} height={580} />
      </div>
      {consonants.map((c, i) => {
        return row(c, i);
      })}
      {value && (
        <>
          <div style={{ position: "absolute", left: 130, top: 324, width: 220, height: 32 }}>
            <SvgText width={248} height={32} label={"中文字源："} text={value[5]} />
          </div>
          <div style={{ position: "absolute", left: 130, top: 360, width: 220, height: 32 }}>
            <SvgText
              width={248}
              height={32}
              label={"罗马注音："}
              text={"[" + (value.length > 6 ? value[6] : value[4]) + "]"}
            />
          </div>
        </>
      )}

      <div
        style={{
          position: "absolute",
          width: 210,
          left: 184,
          top: 384,
          color: "#bbb",
          fontSize: "0.92em",
          fontWeight: 400,
          lineHeight: 2,
        }}
      >
        <p>图的横向称为“行”，纵向称为“段”。5乘10，故称五十音。</p>
        <p>
          平假名除少许例外，均来自汉字的草体，形成于10世纪前后。片假名则多取自汉字偏旁部首的一部分。
        </p>
      </div>
    </div>
  );
};
export default Gojuon;
