import Speaker from "./speaker";

const Sample = ({ index, word, meaning }) => {
  const playSound = (name) => {
    const audio = new Audio("/assets/audio/" + name + ".mp3");
    audio.play();
  };

  return (
    <div key={"block" + index} className="sample">
      <div key={"info" + index} style={{ display: "inline-block" }}>
        <div key={"word" + index}>{word}</div>
        <div key={"meaning" + index}>{meaning}</div>
      </div>
      <div
        key={"spk" + index}
        onClick={() => {
          playSound(word);
        }}
        style={{ display: "inline-block", padding: "4px" }}
      >
        <Speaker size={32} />
      </div>
    </div>
  );
};
export default Sample;
