import React, { useState, useEffect, useRef } from "react";
import { pages, yoon } from "./data";
import "./gojuon.css";
import { SpeakerSvg, ColorDef } from "./svgPart";

const Yoon = () => {
  const [value, setValue] = useState();

  const dwidth = 120;
  const dheight = 75;

  const def = pages[2];
  const fntSize = 120;
  const baseColor = "#eee";
  const noteColor = "#bbb";

  const Item = ({ letter, width, height, font, weight, stroke, strokeWidth }) => {
    return (
      <svg viewBox="0 0 360 225" width={width} height={height}>
        <ColorDef out={false} num={0} />
        <ColorDef out={true} num={0} />
        <rect
          x="1"
          y="1"
          width="358"
          height="223"
          stroke={stroke}
          strokeWidth={strokeWidth}
          fill="none"
        />
        <SpeakerSvg
          num={0}
          center={56}
          rad={54}
          shift={16}
          dx={2}
          dy={2}
          scale={0.7}
          src={letter[0]}
          onSpeak={playSound}
        />
        {letter && (
          <text
            x="222"
            y="140"
            fontFamily={font}
            fontSize={fntSize}
            fontWeight={weight}
            fill={baseColor}
            textAnchor="middle"
          >
            {letter[0]}
          </text>
        )}

        {letter && (
          <text
            x="14"
            y="200"
            fontFamily="serif"
            fontSize={fntSize / 2}
            fontWeight={weight - 200}
            fill={noteColor}
            textAnchor="start"
          >
            {letter[4]}
          </text>
        )}
      </svg>
    );
  };

  const playSound = (name) => {
    const audio = new Audio("/assets/audio/" + name + ".mp3");
    audio.play();
  };

  const select = (cl) => {
    setValue(cl);
  };

  const cell = (ln, cl, i, j) => {
    const n = ln.length;
    const sel = !value ? false : value[0] === cl[0];
    return (
      <div
        key={i + "div" + j}
        onClick={() => {
          select(cl);
        }}
        style={{
          position: "absolute",
          left: (n === 3 ? j + 3 : j) * dwidth + 10,
          top: 8 + i * dheight,
          width: dwidth,
          height: dheight,
          cursor: "pointer",
        }}
      >
        <Item
          letter={cl}
          width={dwidth}
          weight={dheight}
          font={"NotoSerifJP"}
          stroke={sel ? "#f00" : "#0c0"}
          strokeWidth={sel ? 12 : 2}
        />
      </div>
    );
  };

  const row = (ln, i) => {
    return ln.map((cl, j) => {
      return cell(ln, cl, i, j);
    });
  };

  return (
    <div style={{ width: 740, height: 620, backgroundColor: def.base }}>
      {yoon.map((ln, i) => {
        return row(ln, i);
      })}

      <div
        style={{
          position: "absolute",
          width: 170,
          left: 180,
          top: 310,
          color: "#bbb",
          fontSize: "0.92em",
          fontWeight: 400,
          lineHeight: 2,
        }}
      >
        <p>
          拗音由&nbsp;
          <b style={{ fontSize: "1.3em", color: "#fff", fontFamily: "monospace" }}>[i]</b>
          &nbsp;段假名（排除あ、や、わ共7行）以及对应的浊音和ゃ、ゅ、ょ组合构成。
        </p>
        <p>拗音占一个音节。</p>
        <p>拗音的产生与标注古代汉语有关，所以大部分的拗音出现在汉语词汇中。</p>
      </div>
    </div>
  );
};
export default Yoon;
