import React, { useState, useEffect, useRef } from "react";
import { pages, makeQuiz, consonants, vowels } from "./data";
import { SpeakerSvg, ColorDef } from "./svgPart";
import TextButton from "./textButton";
import "./gojuon.css";
const Exam = () => {
  const [mark, setMark] = useState(false);
  const [quiz, setQuiz] = useState(makeQuiz());
  const [selects, setSelects] = useState(new Array(30));
  const [note, setNote] = useState();

  useEffect(() => {
    setSelects(new Array(30));
    setNote("");
  }, [quiz]);

  const def = pages[7];

  const row4 = (inx) => {
    return inx < 18 ? Math.trunc(inx / 6) : Math.trunc((inx - 18) / 4) + 3;
  };

  const col4 = (inx) => {
    return inx < 18 ? inx % 6 : ((inx - 18) % 4) + 2;
  };

  const doSel = (inx, op) => {
    const ops = [...selects];
    if (ops[inx] && ops[inx] === op) {
      ops[inx] = 0;
    } else {
      ops[inx] = op;
    }
    setSelects(ops);
    setMark(false);
    setNote("");
  };

  const onSubmit = () => {
    let pending = 0;
    let wrong = 0;
    for (let i = 0; i < 30; i++) {
      const k = selects[i];
      if (k !== 1 && k !== 2 && k !== 3) {
        pending++;
      } else {
        const q = quiz[i];
        if (q.answer !== q.options[k - 1]) {
          wrong++;
        }
      }
    }
    let msg;
    if (pending === 0 && wrong === 0) msg = "了不起，全部答对";
    else if (pending + wrong === 30) {
      msg = pending > 0 ? pending + "道题没有回答" : "需要继续努力";
    } else {
      const mark = (30 - pending - wrong) / 30;
      if (mark > 0.8) {
        msg = 30 - pending - wrong + "道题回答正确，成绩优秀";
      } else if (mark > 0.6) {
        msg = 30 - pending - wrong + "道题回答正确，成绩不错";
      } else if (pending > 2) {
        msg = 30 - pending - wrong + "道题回答正确，但有" + pending + "道题没有回答";
      } else {
        msg = 30 - pending - wrong + "道题回答正确，加油";
      }
    }
    setNote(msg);
    setMark(true);
  };

  const speak = (src, type, index) => {
    const audio = new Audio("/assets/audio/" + src + ".mp3");
    audio.play();
    if (type) {
      doSel(index, type);
    }
  };

  const LRect = ({ index, num, select }) => {
    return (
      <rect
        cursor="pointer"
        onClick={() => {
          doSel(index, num);
        }}
        x={(num - 1) * 128 + 8}
        y={192}
        width={126}
        height={100}
        fill="transparent"
        stroke={select === num ? "#f00" : "#555"}
        strokeWidth={select === num ? 6 : 3}
      />
    );
  };

  const LText = ({ index, num, text, txtSize, textColor }) => {
    return (
      <text
        cursor="pointer"
        onClick={() => {
          doSel(index, num);
        }}
        x={(num - 1) * 128 + 77}
        y="264"
        fontFamily="sans-serif"
        fontSize={txtSize}
        fontWeight={400}
        fill={textColor}
        textAnchor="middle"
      >
        {text}
      </text>
    );
  };

  const OkMark = () => {
    return <path d="M130,100L180,150 L270,80" fill="none" stroke="#2e0" strokeWidth={14} />;
  };

  const WrongMark = () => {
    // d=20 200-20 130-20
    return <path d="M160,80L240,160 M160,160L240,80" fill="none" stroke="#f00" strokeWidth={12} />;
  };

  const Guess = ({ quiz, index, size, stroke, strokeWidth, font, textColor }) => {
    const defFntSize = 160;
    const txtSize = 76;
    const defTextColor = "#ccc";
    const weight = 500;
    const select = selects[index];
    const ok = select && quiz.options[select - 1] === quiz.answer;
    return (
      <svg viewBox="0 0 400 300" width={(size * 4) / 3} height={size}>
        <ColorDef out={false} num={0} />
        <ColorDef out={true} num={0} />
        <ColorDef out={false} num={1} />
        <ColorDef out={true} num={1} />
        <ColorDef out={false} num={2} />
        <ColorDef out={true} num={2} />
        <ColorDef out={false} num={3} />
        <ColorDef out={true} num={3} />
        <rect
          x="1"
          y="1"
          width="398"
          height="298"
          stroke={stroke}
          strokeWidth={strokeWidth}
          fill="none"
        />
        {quiz.type === 2 ? (
          <SpeakerSvg
            num={1}
            center={56}
            rad={54}
            shift={16}
            dx={130}
            dy={24}
            scale={1.25}
            type={-1}
            src={quiz.base}
            index={index}
            onSpeak={speak}
          />
        ) : (
          <text
            x="200"
            y="160"
            fontFamily={font}
            fontSize={defFntSize}
            fontWeight={weight}
            fill={textColor ? textColor : defTextColor}
            textAnchor="middle"
          >
            {quiz.base}
          </text>
        )}
        <LRect index={index} num={1} select={select} />
        {quiz.type === 3 ? (
          <SpeakerSvg
            num={0}
            center={56}
            rad={54}
            shift={16}
            dx={18}
            dy={188}
            scale={0.9}
            type={1}
            src={quiz.options[0]}
            index={index}
            onSpeak={speak}
          />
        ) : (
          <LText
            index={index}
            num={1}
            text={quiz.options[0]}
            textColor={textColor ? textColor : defTextColor}
            txtSize={txtSize}
          />
        )}

        <LRect index={index} num={2} select={select} />
        {quiz.type === 3 ? (
          <SpeakerSvg
            num={2}
            center={56}
            rad={54}
            shift={16}
            dx={18 + 128}
            dy={188}
            scale={0.9}
            type={2}
            src={quiz.options[1]}
            index={index}
            onSpeak={speak}
          />
        ) : (
          <LText
            index={index}
            num={2}
            text={quiz.options[1]}
            textColor={textColor ? textColor : defTextColor}
            txtSize={txtSize}
          />
        )}
        <LRect index={index} num={3} select={select} />
        {quiz.type === 3 ? (
          <SpeakerSvg
            num={3}
            center={56}
            rad={54}
            shift={16}
            dx={18 + 256}
            dy={188}
            scale={0.9}
            type={3}
            src={quiz.options[2]}
            index={index}
            onSpeak={speak}
          />
        ) : (
          <LText
            index={index}
            num={3}
            text={quiz.options[2]}
            textColor={textColor ? textColor : defTextColor}
            txtSize={txtSize}
          />
        )}
        {mark && ok && <OkMark />}
        {mark && !ok && <WrongMark />}
      </svg>
    );
  };

  return (
    <div style={{ position: "relative", width: 740, height: 620, backgroundColor: def.base }}>
      {quiz.map((q, inx) => {
        return (
          <div
            key={inx + "div"}
            style={{ position: "absolute", left: col4(inx) * 120 + 10, top: row4(inx) * 90 + 10 }}
          >
            <Guess
              quiz={q}
              index={inx}
              size={90}
              weight={500}
              font={"NotoSerifJP"}
              stroke="#47d"
              strokeWidth={4}
            />
          </div>
        );
      })}

      <div
        style={{
          display: "inline-block",
          position: "absolute",
          width: 300,
          height: 32,
          left: 186,
          top: 578,
          fontSize: "1em",
          color: "#eee",
          textAlign: "right",
        }}
      >
        {note}
      </div>

      <TextButton width={90} height={32} text={"递交结果"} x={500} y={570} onAction={onSubmit} />

      <TextButton
        width={90}
        height={32}
        text={"继续出题"}
        x={620}
        y={570}
        onAction={() => {
          setQuiz(makeQuiz());
        }}
      />
    </div>
  );
};
export default Exam;
