import { gradients } from "./data";
import "./gojuon.css";

const TextButton = ({ width, height, text, tone, onAction, x, y }) => {
  const clrs = gradients[tone ? tone : 1];
  const url1 = tone ? "body" + tone : "body1";
  const url2 = tone ? "border" + tone : "border1";

  return (
    <button
      className="iconButton"
      onClick={onAction}
      style={{
        position: "absolute",
        left: x,
        top: y,
        width: width + 6,
        height: height + 6,
      }}
    >
      <svg viewBox="0 0 90 32" width={width} height={height}>
        <linearGradient id={url2} x1="0%" y1="100%" x2="100%" y2="0%">
          <stop stopColor={clrs[0]} offset="0%" />
          <stop stopColor={clrs[1]} offset="100%" />
        </linearGradient>
        <linearGradient id={url1} x1="100%" y1="0%" x2="0%" y2="100%">
          <stop stopColor={clrs[2]} offset="0%" />
          <stop stopColor={clrs[3]} offset="100%" />
        </linearGradient>
        <rect
          x="0.5"
          y="0.5"
          width="89"
          height="31"
          fill={`url('#${url1}')`}
          stroke={`url('#${url2}')`}
          strokeWidth="2"
          rx={6}
          ry={6}
        />
        {text && (
          <text
            x={45}
            y={21}
            textAnchor="middle"
            fontWeight={500}
            fontSize={16}
            fontFamily="serif"
            fill="#eee"
          >
            {text}
          </text>
        )}
      </svg>
    </button>
  );
};
export default TextButton;
