import React, { useState, useEffect, useRef } from "react";
import { pages, gradients, vowels, voiced, pickVoiced } from "./data";
import "./gojuon.css";
import { SpeakerSvg, ColorDef } from "./svgPart";

const Dakuten = () => {
  const [value, setValue] = useState(pickVoiced("a", "g"));
  const dim = 108;
  const def = pages[1];
  const fntSize = 180;
  const baseColor = "#ccc";
  const noteColor = "#aaa";

  const Item = ({ letter, size, font, weight, stroke, strokeWidth }) => {
    return (
      <svg viewBox="0 0 300 330" width={size} height={(size * 11) / 10}>
        <ColorDef out={false} num={0} />
        <ColorDef out={true} num={0} />
        <rect
          x="1"
          y="1"
          width="298"
          height="328"
          stroke={stroke}
          strokeWidth={strokeWidth}
          fill="none"
        />
        <SpeakerSvg
          num={0}
          center={56}
          rad={54}
          shift={16}
          dx={2}
          dy={2}
          scale={0.7}
          src={letter[0]}
          onSpeak={playSound}
        />
        {value && (
          <text
            x="160"
            y="210"
            fontFamily={font}
            fontSize={fntSize}
            fontWeight={weight}
            fill={baseColor}
            textAnchor="middle"
          >
            {letter[0]}
          </text>
        )}

        {value && (
          <text
            x="10"
            y="304"
            fontFamily={font}
            fontSize={fntSize / 3}
            fontWeight={weight - 200}
            fill={baseColor}
            textAnchor="start"
          >
            {letter[1]}
          </text>
        )}

        {value && (
          <text
            x="274"
            y="304"
            fontFamily="serif"
            fontSize={fntSize / 3}
            fontWeight={weight - 200}
            fill={noteColor}
            textAnchor="end"
          >
            {letter[4]}
          </text>
        )}
      </svg>
    );
  };

  const playSound = (name) => {
    const audio = new Audio("/assets/audio/" + name + ".mp3");
    audio.play();
  };

  const select = (v, c) => {
    const o = pickVoiced(v, c);
    setValue(o);
  };

  const cell = (v, c, i, j) => {
    const o = pickVoiced(v, c);
    if (o === null) return <></>;
    const sel = !value ? false : value[2] === v && value[3] === c;
    return (
      <div
        key={i + "div" + j}
        onClick={() => {
          select(v, c);
        }}
        style={{
          position: "absolute",
          left: j * dim + 182,
          top: 8 + (i * dim * 11) / 10,
          width: dim,
          height: dim,
          cursor: "pointer",
        }}
      >
        <Item
          letter={o}
          size={dim}
          weight={500}
          font={"NotoSerifJP"}
          stroke={sel ? "#f00" : "#0c0"}
          strokeWidth={sel ? 12 : 2}
        />
      </div>
    );
  };

  const row = (c, i) => {
    return vowels.map((v, j) => {
      return cell(v, c, i, j);
    });
  };

  return (
    <div style={{ width: 740, height: 620, backgroundColor: def.base }}>
      <div
        style={{
          width: 140,
          marginLeft: "1.4em",
          paddingTop: "0.3em",
          color: "#bbb",
          fontSize: "0.92em",
          fontWeight: 400,
          lineHeight: 2,
        }}
      >
        <p>
          ぱ行属<b style={{ fontSize: "1.1em" }}>半浊音</b>
          。相对其余行，声带振动较弱，音色相对清晰。
        </p>
        <p>
          が行出现在词首以外的位置，要发成<b style={{ fontSize: "1.1em" }}>鼻浊音</b>
          。类似[g]前加[n]，发音变为[ŋ]。
        </p>
      </div>
      {voiced.map((c, i) => {
        return row(c, i);
      })}
    </div>
  );
};
export default Dakuten;
